<template>
  <div class="bgfff out_wrap">
    <el-form :inline="true" :model="form" size="small" label-suffix=":">
      <el-form-item label="客户名称">
        <el-input v-model="form.company_name" placeholder="客户名称" />
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="query">查询</el-button>
      </el-form-item>
    </el-form>
    <zf-table
      :columns="column"
      :data-source="tableList"
      :total="pages.total"
      :page-size="pages.pageSize"
      :page-num="pages.page"
      @change="handlePageChange"
    >
      <!-- 服务费率 -->
      <template
        slot="service_rate"
        slot-scope="{ row }"
      >
        <a class="active_color" @click="views(row)">查看</a>
      </template>
      <!-- 操作 -->
      <template
        slot="action"
        slot-scope="{ row }"
      >
        <a class="active_color" @click="nextDetail(row)">详情</a>
      </template>
    </zf-table>
    <el-dialog
      title="查看服务费率"
      :visible.sync="rateDialogVisible"
      width="40%"
    >
      <rateTable :data-source="rateList" />
    </el-dialog>
  </div>
</template>

<script>
import ZfTable from '@/components/ZfTable/CoreTable'
import rateTable from './components/rateTable'
import column from './columns/list'
import { mapActions, mapGetters, mapMutations } from 'vuex'
export default {
  name: 'List',
  components: {
    ZfTable,
    rateTable
  },
  data() {
    return {
      rateDialogVisible: false,
      column,
      rateList: []
    }
  },
  computed: {
    ...mapGetters('accountManage', ['form', 'pages', 'tableList'])
  },
  mounted() {
    this.query()
  },
  methods: {
    ...mapActions('accountManage', ['partWorkListFn', 'getRateFn']),
    ...mapMutations('accountManage', ['save']),
    query() {
      const query = { ...this.page, ...this.form }
      this.partWorkListFn(query)
    },
    handlePageChange({ pageNum, pageSize, sorter: { prop, order }}) {
      this.save({
        pages: {
          ...this.pages,
          page: pageNum,
          pageSize: pageSize
        }
      })
      this.query()
    },
    nextDetail(row) {
      this.$router.push({
        path: '/client_detail',
        query: {
          company_id: row.company_id,
          name: row.company_name
        }
      })
    },
    async views(row) {
      this.rateList = [] // 先清空数据
      const res = await this.getRateFn({ company_id: row.company_id })
      console.log(res, 'res')
      if (res.code === 200) {
        this.rateDialogVisible = true
        this.rateList = res.data
        console.log(this.rateList, 'this.rateList')
      }
    }
  }
}
</script>

